const API_URL = '/topstories_home.json';

const TopNews = document.getElementById('home-content-data');

const getTopNews = async () => {
  try {
    const res = await fetch(API_URL);
    const data = await res.json();
    return data;
  } catch (e) {
    alert('An error occured', e);
    return [];
  }
};

const updateContainer = (data) => {
  TopNews.innerHTML = '';
  (data.results || []).forEach((news) => {
    const newsContainer = document.createElement('div');
    newsContainer.setAttribute('class', 'article');
    const imageUrl = (news.multimedia && news.multimedia.length > 0)
      ? news.multimedia[0].url 
      : 'default-image-url.jpg';

    

    newsContainer.innerHTML = `
      <h2>${news?.title}</h2>
      ${news?.abstract}
      <a href="${news.url}" target="_blank">Read more</a>
      <img src="${imageUrl}" alt="${news.title}"> 
    `;

    TopNews.appendChild(newsContainer);
  });
};

(async () => {
  const data = await getTopNews();
  updateContainer(data);
})();


